import React from "react";
import "./prologue.css";
import { useNavigate } from 'react-router-dom';

const Prologue = () => {
  const navigate =  useNavigate();
  const handleContinueClick = () => {
    navigate('/Scene1-parts');
  };
  return (
    <>
      <div className="introduction">
        <div className="scene-introduction">
          <h2>Captulo 1</h2>
          <h1>PRLOGO</h1>
          <div className="prologue-container">
          <p>
            [La escena es grotesca demasiada sangre y ha pasado el suficiente tiempo para que despierte el hedor a
            sangre Y sobre todo, partes de la vctima. Desperdigadas entre las ramas est tan desperdigado que
            aparenta no haber muerto aqu, sin embargo, hay la suficiente sangre para saber que simplemente fue un
            homicidio cuidado en su repulsiva esttica]
          </p>
          </div>
          <button className="button_continue" type="submit" onClick={handleContinueClick}>Continuar</button>
        </div>
      </div>
    </>
  );
};

export default Prologue;
